.icon {
  display: block;
  width: 24px;
  height: 24px;

  @include on-tablet {
    width: 32px;
    height: 32px;
  }

  @include hover(transform, scale(1.2));

  &--phone {
    background-image: url(../images/icons/icon-phone.svg);
    background-size: cover;
    position: relative;

    @include on-desktop {
      &::before {
        content: '+1 234 5555-55-55';
        display: block;
        width: 140px;
        font-size: 12px;
        line-height: 16px;
        font-weight: 700;
        letter-spacing: 2px;
        color: $main-color;
        position: absolute;
        top: 10px;
        left: -110px;

        transition: all $transition-duray;
        opacity: 0;
      }

      &:hover::before {
        transform: translate(-48px);
        opacity: 1;
      }
    }
  }

  &--menu {
    background-image: url(../images/icons/icon-burger-menu.svg);
    background-size: cover;
    transition: all $transition-duray;

    &:hover {
      background-image: url(../images/icons/icon-burger-menu-hover.svg);
    }
  }

  &--close {
    background-image: url(../images/icons/icon-close.svg);
    background-size: cover;
  }
}
